 /*eslint-disable*/ 
import React, { Component, useState, useEffect, useRef} from 'react';
import './App.css';

const { kakao } = window;

function Kakaomap() { 
    const [inputText, setInputText] = useState("");
    const [searchPlace, setsearchPlace] = useState("");
    const [showplace, setshowplace] = useState([]);
    // const [mapheight, setmapheight] = useState(flase);
    let location = useRef();

    useEffect(() => {
         let infowindow = new kakao.maps.InfoWindow({zIndex:1});
         const mapcontainer = document.getElementById('kakaomap');
         const options = {
             center: new kakao.maps.LatLng(33.450701, 126.570667),
             level: 3
         };
         const map = new kakao.maps.Map(mapcontainer, options);

         // ì¥ì†Œ ê²€ìƒ‰ ê°ì²´ë¥¼ ìƒì„±
         const ps = new kakao.maps.services.Places();
         // í‚¤ì›Œë“œë¡œ ì¥ì†Œë¥¼ ê²€ìƒ‰
         ps.keywordSearch(searchPlace, placesSearchCB); 

         function placesSearchCB (data, status, pagination) {
            if (status === kakao.maps.services.Status.OK) {
                let bounds = new kakao.maps.LatLngBounds();
                for (let i=0; i<data.length; i++) {
                    displayMarker(data[i]);    
                    bounds.extend(new kakao.maps.LatLng(data[i].y, data[i].x));
                }       
                map.setBounds(bounds);
                 // í˜ì´ì§€ ëª©ë¡ ë³´ì—¬ì£¼ëŠ” displayPagination() ì¶”ê°€
                  displayPagination(pagination);
                  console.log(data);                
                  setshowplace(data);
               //  console.log(showplace);
            } 
          }

        // ê²€ìƒ‰ê²°ê³¼ ëª©ë¡ í•˜ë‹¨ì— í˜ì´ì§€ ë²ˆí˜¸ í‘œì‹œ
        function displayPagination(pagination) {
            var paginationEl = document.getElementById('pagination'),
                fragment = document.createDocumentFragment(),
                i
    
            // ê¸°ì¡´ì— ì¶”ê°€ëœ í˜ì´ì§€ ë²ˆí˜¸ ì‚­ì œ
            while (paginationEl.hasChildNodes()) {
            paginationEl.removeChild(paginationEl.lastChild)
            }
    
            for (i = 1; i <= pagination.last; i++) {
                var el = document.createElement('a')
                el.href = '#'
                el.innerHTML = i
        
                if (i === pagination.current) {
                    el.className = 'on'
                } else {
                    el.onclick = (function (i) {
                    return function () {
                        pagination.gotoPage(i)
                    }
                    })(i)
                }    
                fragment.appendChild(el)
            }
            paginationEl.appendChild(fragment)
        }  

        // ì§€ë„ì— ë§ˆì»¤ë¥¼ í‘œì‹œí•˜ëŠ” í•¨ìˆ˜   
        function displayMarker(place) {
         //   console.log(place);
            let marker = new kakao.maps.Marker({
                map: map,
                position: new kakao.maps.LatLng(place.y, place.x) 
            });
            kakao.maps.event.addListener(marker, 'click', function () {
                infowindow.setContent('<div id="view_p" style="padding:5px;font-size:12px;">' + place.place_name + '</div>')
                infowindow.open(map, marker)
              });
         } 
         
         function setDraggable(flase) {
            // ë§ˆìš°ìŠ¤ ë“œë˜ê·¸ë¡œ ì§€ë„ ì´ë™ ê°€ëŠ¥ì—¬ë¶€ë¥¼ ì„¤ì •í•©ë‹ˆë‹¤
            map.setDraggable(flase);    
        }

     }, [searchPlace] );    
 
     useEffect( () => { } );
    
     return(
         <div className="jinheemap" ref={location} >
           <div id="kakaomap"> 
           </div>
           <form onSubmit={ (e) => { 
                   e.preventDefault();
                   setsearchPlace(inputText);
                   setInputText("");
             } } >
             <input className="search" placeholder="Search Place" 
                onChange={ (e) => { setInputText(e.target.value); } } value={inputText}/> 
             <button className="mapbt" type="submit">ê²€ìƒ‰</button>
            </form>
            <div className="showplace">
              {
                 showplace.map( (item, i) => { 
                     return(
                        <div className="kamapbox" key={i} >
                           <h5>{item.place_name} <span>{item.category_group_name}</span></h5>
                           <h6>{item.road_address_name}</h6>
                           <h6>{item.phone} </h6>       
                        </div>   
                      );                
                 } )
              }  
              <div id="pagination" onClick={ () => {                           
                  //     window.scrollTo({top:location.current, behavior:'smooth'});
              } } ></div>
            </div>
         </div>  
     );
 }

 export default Kakaomap;